<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Funding Batch
      </div>
    </v-card-title>
    <v-card class="ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.ApprovalDate`]="{ item }">
            {{ item.ApprovalDate? toDateTime(item.ApprovalDate):''}}
        </template>
        <template #[`item.nominal`]="{ item }">
            {{ toRupiahString(item.nominal > 0 ? item.nominal : '0')}}
        </template>
        <template #[`item.status`]="{ item }">
          <v-chip small
            class="ma-2"
            :color="getColorStatus(item.status)"
            text-color="white"
          >
            {{ item.status }}
          </v-chip>
        </template>
        <template #[`item.view`]="{ item }">
          <v-btn icon color="success" @click="$router.push({ name: 'FundingShow', params: { id: item.pvcode } })">
            <v-icon>
              mdi-eye
            </v-icon>
          </v-btn>
        </template>
        <template #[`item.action`]="{ item }">
          <div>
            <v-btn depressed elevation="2" class="warning mr-2" @click="download(item.pvcode)">
              <v-icon small class="mr-2">
                mdi-download
              </v-icon> Download
            </v-btn>
            <v-btn v-if="item.NextApproval === auth.user.Jabatan" depressed elevation="2" color="primary" @click="formApproval(item)">
              Approval
            </v-btn>
          </div>
        </template>
      </v-data-table>
    </v-card>
    <FormApprovalVue/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Utils from '@/utils/util'
import FormApprovalVue from '@/components/FundingBatch/FormApproval.vue'
export default {
  name: 'FundingBatch',
  components: {
    FormApprovalVue
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: 'No. Payment Voucher',
        align: 'start',
        sortable: false,
        value: 'pvcode'
      },
      { text: 'Approval Date', sortable: false, value: 'ApprovalDate' },
      { text: 'Jumlah Transaksi', sortable: false, value: 'jumlahtransaksi', align: 'center' },
      { text: 'Nominal', sortable: false, value: 'nominal', align: 'right' },
      { text: 'Funding', sortable: false, value: 'ApprovalStatus', align: 'center' },
      { text: 'Next Approval', sortable: false, value: 'NextApproval' },
      { text: 'Status', sortable: false, value: 'status', align: 'center' },
      { text: 'View', sortable: false, value: 'view', align: 'center' },
      { text: 'Action', sortable: false, value: 'action', align: 'center', width: '320px' }
    ]
  }),
  mounted: function () {
    store.dispatch('fundingbatch/reqData')
  },
  computed: {
    ...mapState('fundingbatch', {
      data: (state) => state
    }),
    ...mapState('auth', {
      auth: (state) => state
    })
  },
  methods: {
    ...mapActions('fundingbatch', [
      'show', 'download', 'formApproval'
    ]),
    toRupiahString (nominal) {
      return Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    getColorStatus (status) {
      if (status === 'NEW') {
        return 'success'
      } else if (status === 'CNCL' || status === 'RJT') {
        return 'red'
      } else if (status === 'APV') {
        return 'primary'
      } else {
        return 'orange'
      }
    }
  }
}
</script>
<style>
.v-btn.v-btn--has-bg.theme--light.v-size--default.primary{
  background-color: blue !important;
}
.v-btn.v-btn--has-bg.theme--light.v-size--default.warning{
  background-color: orange !important;
}
</style>
