<template>
  <div class="text-center">
    <v-dialog
      v-model="state.dialog"
      width="100%"
      max-width="1000"
    >
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Funding Batch Approval <v-btn
            color="primary"
            class="ml-auto"
            text
            @click="state.dialog = false"
          >
            Close
          </v-btn>
        </v-card-title>

        <v-card-text class="pa-4">
          <v-img
          :src="require('@/assets/bfi.png')"
          contain
          width="150px"></v-img>
          <br><br>
          <v-text-field :disabled="state.detail.NextApproval === 'LoanAdmin' ? false : true"
            type="text"
            v-model="state.detail.pvcode_m"
            label="Payment Voucher Code"
            required
            outlined></v-text-field>
            <br>
            <iframe v-if="state.detail.doc_pv" :src="state.detail.doc_pv" width="100%" height="500px" ></iframe>
            <v-file-input v-if="state.detail.NextApproval === 'LoanAdmin'"
              label="Upload Paymentvoucher (.jpg / .png / .pdf)"
              class="mt-2"
              accept="application/pdf, image/*"
              show-size
              v-model="image"
              outlined
            ></v-file-input>
        </v-card-text>

        <v-divider></v-divider>

        <div>
          <v-spacer></v-spacer>
          <v-btn-toggle class="ml-auto mr-auto text-center justify-center" group>
            <v-btn depressed elevation="2" @click="proses('RJT')" class="warning">
              Reject
            </v-btn>
            <v-btn  depressed elevation="2" @click="proses('APV')" class="primary">
              Approval
            </v-btn>
          </v-btn-toggle>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
export default {
  data () {
    return {
      dialog: false,
      image: null
    }
  },
  computed: {
    ...mapState('fundingbatch', {
      state: (state) => state.formApproval
    }),
    ...mapState('auth', {
      auth: (state) => state
    })
  },
  watch: {
    image: function (newVal, oldVal) {
      if (newVal) {
        this.createBase64Image(newVal)
      }
    }
  },
  methods: {
    ...mapActions('fundingbatch', [
      'proses'
    ]),
    checkFile (file) {
      if (file) {
        const ext = file.split('.')
        if (ext[4] === 'pdf') {
          return true
        } else {
          return false
        }
      }
      return true
    },
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.state.detail.doc_pv = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
